.ul {
  /* top: -55px; */
  /* left: -40px; */
  position: relative;
}

.ul li .nav-link {
  color: #C0C0C0;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.ul li .nav-link:focus::after {
  /* color: #2e6dff; */
  transform: scaleX(1);
}

/* .ul li .nav-link:hover::after {
  transform: scaleX(1);
} */
.ul li .nav-link::after {
  content: '';
  display: block;
  height: 2px;
  margin-top: 4px;
  padding: 3px;
  background-color: #2e6dff;
  border-radius: 5px;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease;
}

.nav-underline .nav-link {
  padding-right: 0;
  padding-left: 0;
  /* border-bottom: var(--bs-nav-underline-border-width) solid transparent; */
  border-bottom: 0.4rem solid transparent;
  border-radius: 0.4rem;
}

.dropdown-toggle::after {
  border-top: none !important;
}

.dropdown-menu {
  --bs-dropdown-color: #ffffff !important;
  --bs-dropdown-link-color: #fff !important;
  --bs-dropdown-bg: #000 !important;
  --bs-dropdown-link-hover-color: #efefef !important;
  --bs-dropdown-link-hover-bg: #313234 !important;
}

