.video {
  /* height: 100%; */
  width: 95%;
  border-radius: 11px;
}

#hand {
  animation: owner 3s linear infinite;
}

@keyframes owner {
  from {
    transform: rotateX(12deg);
  }

  to {
    transform: rotateX(-12deg);
  }
}

#hand2 {
  animation: owner1 3s linear infinite;
}

@keyframes owner1 {
  from {
    transform: rotateY(10deg);
  }

  to {
    transform: rotateY(-12deg);
  }
}

#hand3 {
  animation: side 3s linear infinite;
}

@keyframes side {
  from {
    transform: rotateX(-12deg);
  }

  to {
    transform: rotateX(10deg);
  }
}

.plant {
  animation: Myplant 12s linear infinite;
  transform-origin: bottom;
  transform-box: fill-box;
}

@keyframes Myplant {
  from {
    scale: .7;
  }

  to {
    scale: 1.02;
  }
}

.web {
  animation: My 12s linear infinite;
  transform-origin: bottom;
  transform-box: fill-box;
}

@keyframes My {
  from {
    scale: 0.9;
  }

  to {
    scale: 1;
  }
}


